// src/templates/tag-template.js

import React from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import PostItem from '../components/post-item';
import { PostListHeader, PostListWrapper, CenterPos } from '../style/blogMainStyle';

const TagTemplate = ({ data, location, pageContext }) => {
 const posts = data.allMarkdownRemark.nodes;
 return (
  <Layout location={location}>
   <h1>{pageContext.tag}</h1>
   <PostListHeader>
    <span>{posts?.length} 개의 글</span>
   </PostListHeader>
   <PostListWrapper style={{ marginTop: '5rem' }}>
    {(!posts || posts?.length) === 0 && <CenterPos>포스트가 존재하지 않습니다.</CenterPos>}
    {posts?.map((post) => {
     const title = post.frontmatter.title || post.fields.slug;
     return <PostItem key={post.fields.slug} title={title} post={post} />;
    })}
   </PostListWrapper>
  </Layout>
 );
};

export const query = graphql`
 query ($tag: String) {
  allMarkdownRemark(filter: { frontmatter: { tags: { in: [$tag] } } }) {
   nodes {
    excerpt(pruneLength: 80, truncate: true)
    fields {
     slug
    }
    frontmatter {
     date(formatString: "MMMM DD, YYYY")
     title
     description
     category
     icon
     tags
    }
   }
  }
 }
`;

export default TagTemplate;
